import React from "react";
import styled from "styled-components";

import "../styles.css";

import ImageContainer from "./ImageContainer";
import ImageCard from "./ImageCard";
import SecondContainer from "./SecondContainer";
import ThirdContainer from "./ThirdContainer";
import FifthContainer from "./FifthContainer";
import FourthContainer from "./FourthContainer";

import SideMenu from "../../components/utility/SideMenu";
import Footer from "../../components/utility/Footer";
import MyNavBar from "../../components/utility/MyNavBar";
import BackgroundBlob from "../../components/utility/BackgroundBlob";

import {determineScreenState} from "../../constants/helperFunctions";

const blobMap = {
  "wide": {
    blob1: {top: "99%", right: "10%"},
    blob2: {top: "123%", left: "10%"},
    blob3: {top: "167%", right: "10%"},
    blob4: {top: "185%", left: "10%"}
  },
  "full": {
    blob1: {top: "91%", right: "8%"},
    blob2: {top: "113%", left: "8%"},
    blob3: {top: "157%", right: "8%"},
    blob4: {top: "181%", left: "8%"}
  },
  "pacman": {
    blob1: {top: "81%", right: "7%"},
    blob2: {top: "112%", left: "7%"},
    blob3: {top: "164%", right: "7%"},
    blob4: {top: "184%", left: "7%"}
  },
  "half": {
    blob1: {top: "84%", right: "8%"},
    blob2: {top: "113%", left: "8%"},
    blob3: {top: "157%", right: "8%"},
    blob4: {top: "186%", left: "8%"}
  },
  "mobile": {
    blob1: {top: "84%", right: "8%"},
    blob2: {top: "113%", left: "8%"},
    blob3: {top: "157%", right: "8%"},
    blob4: {top: "187%", left: "8%"}
  }
}

const SectionBody = styled.div`
  max-width: 70vw;
  margin-left: auto;
  margin-right: auto;
`;

class Home extends React.Component {
  state = {
    isOpen: false,
    width: 0,
    height: 0
  }

  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener('resize', this.updateWindowDimensions);
  }

  updateWindowDimensions = () => { this.setState({ width: window.innerWidth, height: window.innerHeight }) };

  componentWillUnmount() { window.removeEventListener('resize', this.updateWindowDimensions); }

  navigateSideMenu = () => { this.setState({isOpen: !this.state.isOpen}); }

  render() {
    const {width} = this.state;
    const screenState = determineScreenState(width);
    console.log({screenState})
    return (
      <div className="App">
          <MyNavBar />
          <div onClick={() => this.navigateSideMenu()}>
            <SideMenu right width={width} isOpen={this.state.isOpen}/>
          </div>
          <div className="App-content">
            <div className="fill-header" />
            <ImageContainer width={width} />
            <ImageCard width={width}/>
            <SectionBody>
              <SecondContainer width={width} />
              <ThirdContainer width={width} />
              <FourthContainer width={width} />
              <FifthContainer width={width} />
              <BackgroundBlob style={blobMap[screenState]["blob1"]}/>
              <BackgroundBlob style={blobMap[screenState]["blob2"]}/>
              <BackgroundBlob style={blobMap[screenState]["blob3"]}/>
              <BackgroundBlob style={blobMap[screenState]["blob4"]}/>
            </SectionBody>
            <Footer />
          </div>
      </div>
    );
  }
}

export default Home;
